.ErrorHandler {
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.ErrorHandler__container {
  width: 80%;
  max-width: 600px;
}

.ErrorHandler__icon {
  background-color: #ff6f58;
  color: #fff;
  padding: 2em;
  text-align: center;
}

.ErrorHandler__info {
  background-color: #eee;
  padding: 1em;
}

.ErrorHandler__main-info {
  color: #444;
  margin: 0;
  text-align: center;
}

.ErrorHandler__secondary-info {
  font-size: 0.9em;
}


