.Spinner {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  width: 100%;
}

.Spinner--main {
  -ms-flex-align: center;
      align-items: center;
  background-image: url(../../../assets/whitediamond.png); /* from subtlepatterns.com by Alte Mo */
  z-index: 100;
}

.Spinner--infinite-scroll {
  bottom: 0;
  -ms-flex-align: end;
      align-items: flex-end;
  z-index: 99;
}

.Spinner--infinite-scroll div {
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
}


